@mixin img-one-hundred {
	img {
		max-width: 100%;
		max-height: 100%;
	}
}

@mixin page-background-and-height {
	background: rgba(0, 0, 0, 0.1);
	min-height: calc(100vh - 64px);
	margin: 0 15%;
}

body {
	background: linear-gradient(35deg, white, #e65100)
}

.logo-nav {
	left: 15%;
	height: 100%;

	@include img-one-hundred();
	img {
		object-fit: cover;
	}
}
  
.pokelist {
	@include page-background-and-height();

	margin: 0px 15%;
	padding-top: 15px;
	display: flex;
	flex-direction: row;
	flex-flow: wrap;
	position: relative;
	align-content: flex-start;
	justify-content: space-evenly;
}

.pokecard {
	position: relative;
	display: inline-block;
	flex: 1;
	margin-bottom: 15px;
	color: white;
	width: 300px;
	height: calc(100% - 15px);
	border-radius: 15px;
	padding-bottom: 10px;
	background-color: rgba(0, 0, 0, 0.1);
	transition: 1s;

	@include img-one-hundred();
	.img-wrapper {
		margin: 10px;
		background: rgba(0, 0, 0, 0.1);
		border-radius: 10px;
		transition: 1s;
	}

	&:hover {
		background: rgba(0, 0, 0, 0.5);
		transform: scale(1.05);
		.img-wrapper {
			background: rgba(0, 0, 0, 0);
			transform: scale(1.1)
		}
	}
}

.about-page {
	@include page-background-and-height();

	height: 80vh;

	p {
		margin: 0;
		padding: 10px
	}
}

.pokemon-page {
	@include page-background-and-height();

	display: flex;
	grid-template-columns: 2;
	grid-template-rows: 2;
	grid-column-gap: 0px;
	grid-row-gap: 0px;
	padding: 0 10%
}

.main-wrapper {
	display: inline-block;
	max-height: 700px;
	width: 50%;
	margin-right: 5px;

	.image-wrapper {
		min-height: 10px;
		@include img-one-hundred();
		img {
			width: 400px;
			max-height: 400px;
			background: rgba(0, 0, 0, 0.1);
			border-radius: 15px;
		}
	}

	h2 {
		min-height: 10px;
		text-align: center;
		margin: 15px auto 10px;
		background: rgba(0, 0, 0, 0.1);
		max-width: 400px;
		border-radius: 15px;
		padding-bottom: 15px;
	}

	.sprites-wrapper {
		background: rgba(0, 0, 0, 0.1);
		max-width: 400px;
		min-height: 10px;
		margin: 5px auto;
		border-radius: 15px;

		@include img-one-hundred();
		img {
			width: 100px;
			background: rgba(0, 0, 0, 0.1);
			margin: 5px;
			border-radius: 15px;
			transition: 100ms;

			&:hover {
				cursor: pointer;
				background: rgba(0, 0, 0, 0.2);
				transform: scale(1.15);
			}

		}
	}	
}

.info-wrapper {
	background: rgba(0, 0, 0, 0.1);
	border-radius: 15px;
	margin: 15px 0 auto 5px;
	padding: 5px;
	display: inline-block;
	width: 50%;
	height: 100%;
	text-align: left;
}

.pokemon-table-header {
	text-align: center;
	font-size: 1.2em;
}

.skeleton {
	// z-index: 2;
	// content: '';
	.info-wrapper {
		display: none;
	}
	h2 {
		content: '';
	}
	.main-wrapper {
		@include img-one-hundred();
		img {
			width: 400px;
			max-height: 400px;
		}
	}
}